import TextInputForm from '@/components/forms/TextInputForm';

export default function CustomerInfo() {
    return (
        <>
            <div className="mb-6 grid grid-cols-1 md:grid-cols-3 gap-6">
                <TextInputForm name="full_name" label="اسم العميل" placeholder="ادخل اسم العميل" />
                <TextInputForm
                    label="رقم الهاتف"
                    placeholder="ادخل رقم الهاتف"
                    className="[&>input]:text-end [&>input]:placeholder:text-end"
                    name="phone"
                    type="tel"
                />
                <TextInputForm name="email" label="الإيميل" placeholder="ادخل الإيميل" />
            </div>
            <div className="mb-6 grid grid-cols-1 md:grid-cols-3 gap-6">
                <TextInputForm name="address" label="العنوان" placeholder="ادخل العنوان" />
                <TextInputForm
                    name="personal_id_number"
                    label="رقم البطاقة الشخصية"
                    placeholder="ادخل رقم البطاقة الشخصية"
                />
                <TextInputForm name="driver_license_number" label="رقم رخصة القيادة" placeholder="ادخل رقم الرخصة" />
            </div>
        </>
    );
}
