import { Form } from '@/components/ui/form';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import { PrimaryButton } from '@/components/buttons/PrimaryButton';
import { apiPost } from '@/lib/axios/axios-utils';
import { endpoints } from '@/lib/axios/axios-global';
import { useMemo, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { SecondaryButton } from '@/components/buttons/SecondaryButton';
import CancelPopup from '@/components/Popups/CancelPopup';
import MainHeading from '@/components/heading/MainHeading';
import SuccessPopup from '@/components/Popups/SuccessPopup';
import CustomerInfo from './CustomerInfo';
import ImgInputForm from '@/components/forms/ImgInputForm';
import { AddCustomerFormValues, AddCustomerSchema } from '../schemas/add-customer-schema';
import { Customer } from '@/types/customer.types';
import { EditCustomerFormValues, EditCustomerSchema } from '../schemas/edit-customer-schema';

interface Props {
    customer?: Customer | null;
}

export default function AddCustomerForm({ customer }: Props) {
    const [error, setError] = useState<string | null>(null);
    const [loading, setLoading] = useState(false);
    const [showSuccessPopup, setShowSuccessPopup] = useState(false);
    const [showCancelPopup, setShowCancelPopup] = useState(false);
    const navigate = useNavigate();

    const defaultValues = useMemo(
        () => ({
            full_name: customer?.full_name || '',
            phone: customer?.phone || '',
            email: customer?.email || '',
            address: customer?.address || '',
            personal_id_number: customer?.personal_id_number || '',
            driver_license_number: customer?.driver_license_number || '',
        }),
        [customer],
    );

    const form = useForm<AddCustomerFormValues | EditCustomerFormValues>({
        resolver: zodResolver(customer ? EditCustomerSchema : AddCustomerSchema),
        defaultValues,
    });

    const handleAddAnother = () => {
        setShowSuccessPopup(false);
    };

    const onSubmit = async (data: AddCustomerFormValues | EditCustomerFormValues) => {
        setLoading(true);
        setError(null); // Clear previous errors
        try {
            await apiPost({
                endpoint: customer ? endpoints.customer.update + customer.id : endpoints.customer.create,
                body: data,
                config: { headers: { 'Content-Type': 'multipart/form-data' } },
            });
            setShowSuccessPopup(true);
        } catch (error: any) {
            if (error.response?.data?.errors) {
                // Handle field-specific errors
                Object.entries(error.response.data.errors).forEach(([field, messages]) => {
                    form.setError(field as any, {
                        type: 'manual',
                        message: (messages as string[]).join(', '),
                    });
                });
            } else {
                // Handle generic error
                let errorMessage = 'حدث خطأ أثناء إرسال البيانات. يرجى المحاولة مرة أخرى.';
                if (error.response) {
                    errorMessage = error.response.data.msg || errorMessage;
                } else if (error.request) {
                    errorMessage = 'تعذر الاتصال بالخادم. يرجى التحقق من اتصالك بالإنترنت.';
                }
                setError(errorMessage);
            }
        } finally {
            setLoading(false);
        }
    };

    return (
        <>
            <Form {...form}>
                <form onSubmit={form.handleSubmit(onSubmit)} className="mb-8">
                    <MainHeading text="بيانات العميل" />
                    <CustomerInfo />
                    <MainHeading text="المستندات الشخصية" className="mb-6" />
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                        <ImgInputForm
                            name="personal_id_img_front"
                            maxSize={10 * 1024 * 1024}
                            multiple={false}
                            label="الهوية الشخصيه ( الامام )"
                        />
                        <ImgInputForm
                            name="personal_id_img_back"
                            maxSize={10 * 1024 * 1024}
                            multiple={false}
                            label="الهوية الشخصية ( الخلف )"
                        />
                        <ImgInputForm
                            name="driver_license_img_front"
                            maxSize={10 * 1024 * 1024}
                            multiple={false}
                            label="الرخصة ( الامام )"
                        />
                        <ImgInputForm
                            name="driver_license_img_back"
                            maxSize={10 * 1024 * 1024}
                            multiple={false}
                            label="الرخصة ( الخلف )"
                        />
                    </div>
                    {error && (
                        <div className="mb-4 p-4 bg-red-100 border border-red-400 text-red-700 rounded-lg">
                            <div className="flex justify-between items-center">
                                <span>{error}</span>
                                <button onClick={() => setError(null)} className="text-red-700 hover:text-red-900">
                                    ×
                                </button>
                            </div>
                        </div>
                    )}
                    <div className="flex flex-row justify-between items-center mt-8">
                        {customer ? (
                            <SecondaryButton
                                type="button"
                                className="xl:px-[101px] xl:py-[18px] text-[#A21515] border border-red-error-50 xl:font-bold bg-red-error-50"
                                onClick={() => setShowCancelPopup(true)}
                            >
                                إلغاء الطلب
                            </SecondaryButton>
                        ) : (
                            <SecondaryButton
                                type="button"
                                className="xl:px-[64.5px] xl:py-[18px] border border-r-red-error-50 bg-red-error-50 text-[#A21515] xl:font-bold"
                                onClick={() => setShowCancelPopup(true)}
                            >
                                الغاء
                            </SecondaryButton>
                        )}
                        <div>
                            {customer ? (
                                <PrimaryButton
                                    className="xl:px-[107px] xl:py-[18px] xl:font-bold ms-6"
                                    disabled={loading}
                                >
                                    {loading ? 'جاري التحميل...' : 'حفظ التغييرات'}
                                </PrimaryButton>
                            ) : (
                                <PrimaryButton
                                    className="xl:px-[107px] xl:py-[18px] xl:font-bold ms-6"
                                    disabled={loading}
                                >
                                    {loading ? 'جاري التحميل...' : 'اضافة'}
                                </PrimaryButton>
                            )}
                        </div>
                    </div>
                </form>
            </Form>

            <CancelPopup
                open={showCancelPopup}
                onClickButton1={() => {
                    setShowCancelPopup(false);
                }}
                onClickButton2={() => {
                    navigate('/customer-relations');
                    setShowCancelPopup(false);
                }}
                text1={customer ? 'إلغاء تعديل بيانات العميل' : 'إلغاء إضافة عميل'}
                text2={
                    customer
                        ? 'أنت على وشك إلغاء عملية تعديل بيانات العميل. سيتم تجاهل جميع البيانات المدخلة، هل أنت متأكد؟'
                        : 'أنت على وشك إلغاء عملية إضافة عميل جديد دون إكمال البيانات المطلوبة. سيتم تجاهل جميع البيانات المدخلة، هل أنت متأكد؟'
                }
                textButton1="إكمال الطلب"
                textButton2="إلغاء الطلب"
            />

            <SuccessPopup
                open={showSuccessPopup}
                text1={customer ? 'تم تعديل البيانات بنحاح' : 'تم إضافة عميل جديد بنحاح'}
                text2={customer ? 'تم تعديل بيانات العميل الجديد بنجاح.' : 'تم تسجيل بيانات العميل الجديد بنجاح.'}
                textButton2={customer ? 'تعديل بيانات اخرى' : 'إضافة عميل أخر'}
                textButton1="العودة لقائمة العملاء"
                onAddAnother={handleAddAnother}
                onReturnToList={() => {
                    navigate('/customer-relations');
                    setShowSuccessPopup(false);
                }}
            />
        </>
    );
}
